@import url(https://fonts.googleapis.com/css?family=Roboto|Ubuntu);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Ubuntu', sans-serif;
  font-family: 'Roboto', sans-serif;
}

.article {
    position: relative;
    width: calc(100vw - 100px);
    height: calc(100vh - 40px);
    overflow-x: hidden;
    display: grid;
    grid-template-columns: 70% 30%;
    z-index: 2;
    background-color: #eeeeee;
    padding: 20px 50px;
}

.articleImg {
    width: 100%;
    height: calc(100vh - 100px );
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.articleTxt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    overflow: hidden;
    padding: 50px 50px 50px 0;
}

.articleTxt h1 {
    margin-bottom: 50px;
    font-size: 100px;
    font-family: 'Ubuntu', sans-serif;
    color: #00adb5;
}

.article p {
    color: #393e46;
    margin: 10px 0; 
}

.article b {
    color: #222831;
}

.bigTitle {
    position: absolute;
    font-size: 20em;
    left: -30px;
    top: -70px;
    z-index: -1;
    color: #00acb52a;
    height: calc(100vh + 70px);
    overflow: hidden;
}
body {
    background-color: #222831;
}


.searchField{
    width: 100%;
    height: 100px;
    display: flex;
}

.searchField input {
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    text-align: center;
    background-color: transparent;
    color: #eeeeee;
    font-weight: bold;
    font-size: 30px;
}

.searchField input::-webkit-input-placeholder {
    color: #eeeeeeb4;
}

.searchField input::-ms-input-placeholder {
    color: #eeeeeeb4;
}

.searchField input::placeholder {
    color: #eeeeeeb4;
}

.searchContainer {
    background-color: #393e46;
    cursor: pointer;
}

.searchContainer img {
    height: 3vw;
    position: absolute;
    left: calc(50% - 1.5vw);
    top: calc(50% - 1.5vw);
}

.searchResults {
    width: 100%;
    max-width: 700px;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.itemContainer {
    width: 90%;
    min-height: 100px;
    margin: 20px 0;
    padding: 20px;
    background-color: #eeeeee;
    border-radius: 5px;
    cursor: pointer;
}

.itemContainer h2 {
    font-size: 30px;
    margin-bottom: 10px;
    font-weight: bold;
}

.itemContainer p {
    color: rgba(41, 42, 47, 0.7);
}
